import React, { useEffect, useState } from "react";

import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import FormHelperText from "@mui/material/FormHelperText";
import Grid from "@mui/material/Grid";
import Typography from "@mui/material/Typography";

import { get, isEmpty } from "lodash";
import { useCookies } from "react-cookie";
import { useGoogleReCaptcha } from "react-google-recaptcha-v3";
import OtpInput from "react-otp-input";
import { useDispatch, useSelector } from "react-redux";
import { useLocation, useNavigate } from "react-router-dom";
import { toast } from "react-toastify";

import {
  setAuthToken,
  setIsLoggedIn
} from "../../../store/slice/auth/authentication/authSlice";
import {
  postOtpVerification,
  postResendOtp
} from "../../../store/slice/auth/signup/otpVerificationSlice";
import { getUserData } from "../../../store/slice/auth/signup/signupSlice";
import ComponentStyle from "../../../styles/ComponentStyle.module.css";
import { expireTime } from "../../hooks/cookie";
import translate from "../../hooks/translate";

const OtpVerification = () => {
  const [otpCode, setOtpCode] = useState("");
  const [erorrAlert, setErorrAlert] = useState(false);

  const dispatch = useDispatch();
  const { state } = useLocation();

  const navigate = useNavigate();

  const [, setCookie] = useCookies();
  const { executeRecaptcha } = useGoogleReCaptcha();

  const userDetails = useSelector(getUserData);

  useEffect(() => {
    if (
      get(state, "isOtpVerified", null) === null ||
      isEmpty(get(userDetails, "userId", "")) === true
    ) {
      navigate("/signup");
    }
  });

  const handleChange = (code) => {
    setOtpCode(code);
  };

  const handleSubmitOtp = async () => {
    if (otpCode.length === 6) {
      setErorrAlert(false);

      const userOtpData = {
        userId: get(userDetails, "userId", ""),
        otp: otpCode,
      };

      const res = await dispatch(postOtpVerification(userOtpData))
        .unwrap()
        .then((result) => {
          if (
            get(result, "status", false) &&
            get(result, "data.token", "") !== ""
          ) {
            const userData = {
              email: get(userDetails, "email"),
              token: get(result, "data.token"),
            };
            setCookie("consultant_data", userData, {
              path: "/",
              expires: expireTime,
            });
            dispatch(setAuthToken(result.data.token));
            dispatch(setIsLoggedIn(true));
            navigate("/consultant-info");
            setOtpCode("");
          } else {
            toast.error(result.message);
          }
        })
        .catch((error) => {
          console.log("OTP error=======>", error);
        });
      return res;
    } else {
      setErorrAlert(true);
    }
  };

  const otpResend = async () => {
    if (!executeRecaptcha) {
      toast.error("You are not Authorized");
      return;
    }
    const token = await executeRecaptcha("yourAction");

    const payload = {
      email: get(userDetails, "email", ""),
      googleRecaptchaToken: token,
    };
    const res = await dispatch(postResendOtp(payload))
      .unwrap()
      .then((result) => {
        if (result.status) {
          toast.success(result.message, {
            position: toast.POSITION.TOP_CENTER,
          });
        }
      })
      .catch((error) => {
        console.log("OTP error=======>", error);
      });
    return res;
  };
  return (
    <>
      <Box component="div" className="h-100">
        <Grid container>
          <Grid item sm={9} md={10} lg={8} sx={{ mx: { md: "0", xs: "auto" } }}>
            <Typography variant="h4" align="center" mb={2}>
            {translate("common_otp_verification")}
            </Typography>
            <Typography variant="body1" align="center" mb={2}>
            {translate("common_code_sent_to")}
              <Typography
                variant="body1"
                color="primary"
                component="span"
                ml={1}
              >
                {userDetails.email}
              </Typography>
            </Typography>
            <Grid container spacing={2} justifyContent="center">
              <Grid item xs={12}>
                <OtpInput
                  value={otpCode}
                  onChange={handleChange}
                  numInputs={6}
                  separator={<span style={{ width: "8px" }}></span>}
                  isInputNum={true}
                  shouldAutoFocus={true}
                  className={ComponentStyle.otp_inputs}
                  containerStyle={{
                    alignItems: "center",
                    justifyContent: "center",
                  }}
                  hasErrored={erorrAlert}
                  errorStyle={{ color: "#b71c1c", borderColor: "#b71c1c" }}
                  inputStyle={{
                    border: "1px solid #CFD3DB",
                    borderRadius: "8px",
                    width: "40px",
                    height: "40px",
                    fontSize: "16px",
                    color: "#777E89",
                    fontWeight: "400",
                    caretColor: "#777E89",
                    margin: "0 auto",
                  }}
                  focusStyle={{
                    border: "1px solid #CFD3DB",
                    outline: "none",
                  }}
                />
                {erorrAlert && (
                  <FormHelperText
                    error={true}
                    sx={{ textAlign: "center", mt: 1 }}
                  >
                    {translate(
                      "common_error_all_fields_are_required"
                    )}
                  </FormHelperText>
                )}
                <Typography variant="body1" align="center" mt={2}>
                {translate("common_didn't_receive_code")} ?
                  <Typography
                    variant="body1"
                    color="primary"
                    component="span"
                    className="cursor-pointer"
                    ml={1}
                    onClick={otpResend}
                  >
                    {translate("common_request_again")}
                  </Typography>
                </Typography>
              </Grid>
              <Grid item xs={12}>
                <Button
                  variant="contained"
                  fullWidth
                  type="submit"
                  onClick={handleSubmitOtp}
                >
                  {translate("common_verify_now")}
                </Button>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </Box>
    </>
  );
};

export default OtpVerification;
