import Box from "@mui/material/Box";
import Container from "@mui/material/Container";
import Grid from "@mui/material/Grid";
import React from "react";
import { GoogleReCaptchaProvider } from "react-google-recaptcha-v3";
import ImageComponent from "../../../components/auth/signup/ImageComponent";
import OtpVerification from "../../../components/auth/signup/OtpVerification";

const OtpVerify = () => {
  const GOOGLE_RECAPTCHA_KEY = process.env.REACT_APP_GOOGLE_RECAPTCHA_KEY;

  return (
    <>
      <Box
        component="section"
        sx={{ py: { sm: 6.4, xs: 4 } }}
        className="d-flex align-items-center justify-content-center w-100"
      >
        <Container maxWidth="xl">
          <Grid
            container
            spacing={2}
            sx={{ justifyContent: { md: "space-between", xs: "center" } }}
            column={12}
          >
            <Grid item lg={6} md={6}>
              <GoogleReCaptchaProvider reCaptchaKey={GOOGLE_RECAPTCHA_KEY}>
                <OtpVerification />
              </GoogleReCaptchaProvider>
            </Grid>
            <Grid item lg={5} md={6}>
              <ImageComponent />
            </Grid>
          </Grid>
        </Container>
      </Box>
    </>
  );
};

export default OtpVerify;
